/* Icon Container */
.majestic-frame {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    -ms-flex-item-align: center;
        align-self: center;
   
}
/* SVG Icon */
.majestic-frame, svg.arrow {
    fill: black;
    -webkit-transition: 
        all 0.3s cubic-bezier(.25,.8,.25,1);
    -o-transition: 
        all 0.3s cubic-bezier(.25,.8,.25,1);
    transition: 
        all 0.3s cubic-bezier(.25,.8,.25,1);
    -webkit-filter: 
        drop-shadow( 
            4px 4px 4px rgba(0,0,0,0.23) 
        );
            filter: 
        drop-shadow( 
            4px 4px 4px rgba(0,0,0,0.23) 
        );
}
.majestic-frame, svg.arrow:hover {
     -webkit-filter: 
        drop-shadow( 
            7.3px 7.3px 7.3px rgba(0,0,0,0.25) 
        );
             filter: 
        drop-shadow( 
            7.3px 7.3px 7.3px rgba(0,0,0,0.25) 
        );
} 


